<template>
	<section>
		<!--<div id="main" style="width: 600px;height:333px; margin-left: 100px;"></div>
		<div class="oiltype" v-for="item in oilTypes">
			<span>{{item}}</span>
			<input type="checkbox" v-model="arr" :value="item"/>
		</div>
		<span>{{arr}}</span>-->
		<div class="inf-zst-t">
			<div class="inf-info tieshui">当前位置：<a href="">首页 &gt;</a><a href="javascript:;">最新官价走势图</a></div>
	      	<span class="inf-zt-gb"><a href="javascript:;" @click="backFn">×</a></span>
	      	<div class="cg-sc-zst">
	      		<div id="main" style="width:600px;height:400px;"  v-show="num == 0"></div>
	      		<div class="officalData" v-show="num ==1">
		      		<p>暂无有关数据</p>
		      	</div>
		      	<div class="officalData" v-show="num ==2">
		      		<p>请至少选择一种油种</p>
		      	</div>
	      	</div>
	      	
	        <ul class="cg-sc-xz clearfix">
	           	<li v-for="item in oilTypes"><input type="checkbox" class="c-check" :value="item" v-model="arr">{{item}}</li>
	        </ul>
	        <div class="block">
			  <span class="demonstration">请选择年份</span>
			  <el-date-picker
			    v-model="year"
			    align="right"
			    type="year"
			    value-format="yyyy"
			    placeholder="选择年">
			  </el-date-picker>
			</div>
	    </div>
	   
	</section>
</template>

<script>
	import echarts from 'echarts'
	import { officialPrice,officialTrend } from  '../../api/info'
	export default{
		data(){
			return{
				options:{
					
					 tooltip: {
				        trigger: 'axis',
				        axisPointer: {
				            type: 'cross',
				            crossStyle: {
				                color: '#999'
				            }
				        }
				    },
				    xAxis: [
				        {
				            type: 'category',
				            data: []
				        }
				    ],
				    yAxis: [
				        {
				            type: 'value',
//				            name: '水量',
//				            min: 0,
//				            max: 250,
//				            interval: 50,
//				            axisLabel: {
//				                formatter: '{value} ml'
//				            }
				        },
				        {
				            type: 'value',
//				            name: '温度',
////				            min: 0,
//				            max: 25,
//				            interval: 5,
//				            axisLabel: {
//				                formatter: '{value} °C'
//				            }
				        }
				    ],
				    series: [
				        {
				            name:'',
				            type:'line',
				            data:[]
				        },
				        {
				            name:'',
				            type:'line',
				            data:[]
				        },
				        {
				            name:'',
				            type:'line',
//				            yAxisIndex: 1,
				            data:[]
				        }
				    ]
				},
				oilTypes:[],
				arr:[],
				year:'2018',
				num :0
			}
		},
		methods:{
			typeFn(){
				var params = {
					pricingDate:this.year
				}
				officialPrice(params).then(res =>{
					console.log(res)
					this.oilTypes = res.datas
					this.arr[0]= res.datas[0]
					var para={
						'crudeNameArray':this.arr,
						'pricingDate':this.year
					}
					var myChart = echarts.init(document.getElementById('main'));
					var option12 = $.extend(true, option12, this.options);
	   				officialTrend(para).then(res=>{
	   					option12.series[0].name = res.datas[0].crudeNameE
	   					for(var i = 0;i<res.datas[0].dataList.length;i++){
	   						option12.xAxis[0].data.push(res.datas[0].dataList[i].pricingDate)
	   						option12.series[0].data.push(res.datas[0].dataList[i].pricing)
	   					}
	   					 myChart.setOption(option12);
	   				})
				})
				
			},
			backFn(){
				this.$router.push({path:'/oil'})
			}
		},
		watch:{
			arr:function(val){
				if(val.length > 3){
					this.arr = val.slice(0,3)
//					alert('做多只能选择三条')
					this.$message({
			          message: '最多只能选择三种',
			          type: 'warning'
			        });
				}else{
					var myChart = echarts.init(document.getElementById('main'));
					var option12 = $.extend(true, option12, this.options);
					var para={
						'crudeNameArray':val,
						'pricingDate':this.year
					}
	   				officialTrend(para).then(res=>{
//	   					console.log(res)
	   					if(val.length==0){
							this.num = 2
							return false
						}
	   					if(res.datas.length == 0){
	   						this.num = 1
	   					}else if(res.datas.length != 0){
	   						this.num = 0
	   						for(var i = 0;i<res.datas.length;i++){
	   							if(res.datas[i].dataList == null){
	   								continue
	   							}
	   							if(res.datas[i].isExy){
	   								option12.series[i].yAxisIndex=1
	   							}else{
	   								option12.series[i].yAxisIndex=0
	   							}
		   						option12.series[i].name = res.datas[i].crudeNameE
		   						for(var j=0;j<res.datas[i].dataList.length;j++){
		   							option12.series[i].data.push(res.datas[i].dataList[j].pricing)
		   							if(i==0){
		   								option12.xAxis[0].data.push(res.datas[0].dataList[j].pricingDate)
		   							}	
		   						}
		   					}
		   					myChart.setOption(option12);
	   					}	
	   				})
				}
			},
			year:function(newVal){
				var myChart = echarts.init(document.getElementById('main'));
				var option12 = $.extend(true, option12, this.options);
				var para={
					'crudeNameArray':this.arr,
					'pricingDate':newVal
				}
   				officialTrend(para).then(res=>{
   					if(res.datas.length == 0){
   						this.num = 1
   					}else if(res.datas.length != 0){
   						this.num = 0
   						for(var i = 0;i<res.datas.length;i++){
	   						option12.series[i].name = res.datas[i].crudeNameE
	   						for(var j=0;j<res.datas[i].dataList.length;j++){
	   							option12.series[i].data.push(res.datas[i].dataList[j].pricing)
	   							if(i==0){
	   								option12.xAxis[0].data.push(res.datas[0].dataList[j].pricingDate)
	   							}	
	   						}
	   					}
	   					myChart.setOption(option12);
   					}	
   				})
// 				var params = {
//					pricingDate:newVal
//				}
// 				officialPrice(params).then(res =>{
// 					console.log(res)
//					this.oilTypes = res.datas
//					this.arr[0]= res.datas[0]
//				})
			}
		},
		created(){
			
		},
		mounted(){
			this.typeFn()
			
		}
	}
</script>

<style>
	.oiltype{
		float: left;
	}
	#main{
		
		margin-left: 14%;
	}
	.block{
		font-size: 14px;
		text-align: center;
	}
	.el-year-table td.current{
	position:relative; 
     top:inherit !important; 
     left:inherit !important; 
     width:auto !important; 
     height:auto !important; 
     border:none !important; 
     box-shadow:none !important; 
     border-radius:inherit !important; 
}     
	.officalData{
		font-size: 18px;
		height: 300px;
		text-align: center;
		line-height: 300px;
	}
	.tieshui{
		position: absolute !important;
		left: 50px;
    	top: 11px;
	}
</style>


